import React, { useEffect, useState } from 'react';
import axios from 'axios';

const StoreDetail = ({ username, sno }) => {
    const [store, setStore] = useState(null); // 선택된 스토어 정보를 저장할 상태
    const [loading, setLoading] = useState(true); // 로딩 상태
    const [error, setError] = useState(null); // 오류 메시지 상태

    // 선택한 스토어의 상세 정보를 가져오는 함수
    const fetchStoreDetail = async () => {
        try {
            const response = await axios.get(`/api/store/read`, {
                params: { username, sno },
            });
            setStore(response.data); // API 응답에서 스토어 정보 추출
        } catch (err) {
            setError(err.message); // 오류 발생 시 메시지 설정
        } finally {
            setLoading(false); // 로딩 종료
        }
    };

    // 컴포넌트가 마운트될 때 호출
    useEffect(() => {
        fetchStoreDetail();
    }, [username, sno]);

    // 로딩 중일 때
    if (loading) return <div>Loading...</div>;
    // 오류 발생 시
    if (error) return <div>Error: {error}</div>;

    return (
        <div>
            <h2>{store.p_name}</h2>
            <p>Address: {store.p_address}</p>
            <p>Category: {store.p_category}</p>
            <p>Rating: {store.p_star}</p>
            {/* 추가적인 스토어 정보 표시 가능 */}
        </div>
    );
};

export default StoreDetail;
